<template>
  <v-navigation-drawer
    location="right"
    v-model="localDrawer"
    width="1000"
    temporary
    disable-route-watcher
  >
    <v-card class="fill-height">
      <v-card-title :class="`bg-${providerName} white--text`">
        <b>{{ account.agent_username }}</b>
      </v-card-title>
      <v-card-text class="fill-height">
        <PESSLDrawer
          v-if="providerName === 'PESSL'"
          :account="account"
          :drawer="drawer"
        ></PESSLDrawer>
        <APASDrawer
          v-else-if="providerName === 'APAS'"
          :account="account"
          :drawer="drawer"
        ></APASDrawer>
        <WiseCropDrawer
          v-else-if="providerName === 'WiseCrop'"
          :account="account"
          :drawer="drawer"
        ></WiseCropDrawer>
        <TerraproDrawer
          v-else-if="providerName === 'TerraPro'"
          :account="account"
          :drawer="drawer"
        ></TerraproDrawer>
      </v-card-text>
    </v-card>
    <!-- <v-list-item class="bg-primary white--text"> </v-list-item>
    <v-divider></v-divider> -->
  </v-navigation-drawer>
</template>

<script>
import PESSLDrawer from "./ProvidersDrawer/PESSLDrawer.vue";
import APASDrawer from "./ProvidersDrawer/APASDrawer.vue";
import WiseCropDrawer from "./ProvidersDrawer/WiseCropDrawer.vue";
import TerraproDrawer from "./ProvidersDrawer/TerraproDrawer.vue";

export default {
  name: "ProvidersDrawer",
  components: { PESSLDrawer, APASDrawer, WiseCropDrawer, TerraproDrawer },
  props: {
    drawer: {
      type: Boolean,
      default: false,
    },
    account: {
      type: Object,
    },
    accountWS: {
      type: Object,
      default: () => ({
        type: "FeatureCollection",
        features: [],
      }),
    },
    isLoading: {
      type: Boolean,
      default: true,
    },
  },

  computed: {
    localDrawer: {
      get() {
        return this.drawer;
      },
      set(value) {
        this.$emit("update", value);
      },
    },
    providerName() {
      return this.account.dataprovider_name;
    },
    drawerWidth() {
      return "";
    },
  },
};
</script>

<style></style>
