<template>
  <v-row>
    <v-col cols="12" class="d-flex justify-center">
      <v-sheet rounded border class="pa-5" width="35rem">
        <v-form v-model="valid">
          <div class="text-subtitle-1 text-medium-emphasis">Código Dropbox</div>
          <v-text-field
            v-model="dropbox_code"
            class="mt-4"
            density="compact"
            placeholder="Código Dropbox"
            prepend-inner-icon="mdi-dropbox"
            variant="outlined"
            :rules="rules.dropbox_code"
          ></v-text-field>
        </v-form>
        <v-btn
          block
          color="secondary"
          @click="getAPASAccount()"
          :disabled="!valid"
          :loading="localIsLoading"
          prepend-icon="mdi-account-check"
          >Verificar conta</v-btn
        >
      </v-sheet>
    </v-col>
  </v-row>
</template>

<script>
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "AddAPASAccount",
  props: {
    provider: {
      type: Object,
    },
    providerAccount: {
      type: Object,
    },
    stepValid: {
      type: Boolean,
    },
    isLoading: {
      type: Boolean,
    },
  },
  setup() {
    const generalStore = useGeneralStore();
    return { generalStore };
  },
  data() {
    return {
      valid: false,
      dropbox_code: null,
      rules: {
        dropbox_code: [(v) => !!v || "Insira o código do Dropbox"],
      },
    };
  },
  // watch: {
  //   stepValid() {
  //     if (this.stepValid) {
  //       this.getAPASAccount();
  //     }
  //   },
  // },
  computed: {
    localProviderAccount: {
      get() {
        return this.providerAccount;
      },
      set(value) {
        this.$emit("update:providerAccount", value);
      },
    },
    localStepValid: {
      get() {
        return this.stepValid;
      },
      set(value) {
        this.$emit("update:stepValid", value);
      },
    },
    localIsLoading: {
      get() {
        return this.isLoading;
      },
      set(value) {
        this.$emit("update:isLoading", value);
      },
    },
  },
  methods: {
    async getAPASAccount() {
      this.localIsLoading = true;
      axios
        .request({
          url: "https://api.dropboxapi.com/2/users/get_current_account",
          method: "post",
          headers: {
            Authorization: `Bearer ${this.dropbox_code}`,
            "Content-Type": "application/json",
          },
        })
        .then((response) => {
          console.log(response);
          let data = response.data;
          let _account = {
            provider: this.provider,
            username: data.name.abbreviated_name,
            name: data.name.display_name,
            email: data.email,
            phone: null,
            city: null,
            address: null,
            agent_properties: {
              dataprovider_id: this.provider.dataprovider_id,
              api_auth: {
                dropbox_code: this.dropbox_code,
              },
            },
          };
          this.localProviderAccount = _account;
          this.localIsLoading = false;
          this.generalStore.successSnackbar(
            "Conta APAS verificada com sucesso!"
          );
          this.$emit("nextStep");
        })
        .catch((error) => {
          console.log(error);
          this.localIsLoading = false;
          this.generalStore.errorSnackbar(
            "Erro ao buscar informações de conta APAS!"
          );
        });
    },
  },
};
</script>

<style></style>
