<template>
  <v-row>
    <v-col cols="12" class="d-flex justify-center">
      <v-sheet rounded border class="pa-5" width="25rem">
        <v-form v-model="valid" lazy-validation>
          <div class="text-subtitle-1 text-medium-emphasis">Username</div>
          <v-text-field
            v-model="j_username"
            class="mt-4"
            density="compact"
            placeholder="Username"
            prepend-inner-icon="mdi-account"
            variant="outlined"
            :rules="rules.j_username"
          ></v-text-field>
          <div class="text-subtitle-1 text-medium-emphasis">Password</div>
          <v-text-field
            v-model="j_password"
            class="mt-4"
            density="compact"
            placeholder="Password"
            prepend-inner-icon="mdi-lock"
            variant="outlined"
            :rules="rules.j_password"
            :append-inner-icon="visible ? 'mdi-eye-off' : 'mdi-eye'"
            :type="visible ? 'text' : 'password'"
            @click:append-inner="visible = !visible"
          ></v-text-field>
        </v-form>
        <v-btn
          block
          color="secondary"
          @click="getTerraproAccount()"
          :disabled="!valid"
          :loading="localIsLoading"
          append-icon="mdi-account-check"
          >Verificar conta</v-btn
        >
      </v-sheet>
    </v-col>
  </v-row>
</template>

<script>
import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "AddTerraproAccount",
  props: {
    provider: {
      type: Object,
    },
    providerAccount: {
      type: Object,
    },
    stepValid: {
      type: Boolean,
    },
    isLoading: {
      type: Boolean,
    },
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return { accountStore, generalStore };
  },
  data() {
    return {
      visible: false,
      valid: false,
      j_username: null,
      j_password: null,
      rules: {
        j_username: [(v) => !!v || "Insira o username da conta Terrapro"],
        j_password: [(v) => !!v || "Insira a senha da conta Terrapro"],
      },
    };
  },
  computed: {
    localProviderAccount: {
      get() {
        return this.providerAccount;
      },
      set(value) {
        this.$emit("update:providerAccount", value);
      },
    },
    localStepValid: {
      get() {
        return this.stepValid;
      },
      set(value) {
        this.$emit("update:stepValid", value);
      },
    },
    localIsLoading: {
      get() {
        return this.isLoading;
      },
      set(value) {
        this.$emit("update:isLoading", value);
      },
    },
  },
  methods: {
    getTerraproAccount() {
      this.localIsLoading = true;
      axios
        .request({
          method: "POST",
          url: "/api/terrapro/auth",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: {
            j_username: this.j_username,
            j_password: this.j_password,
          },
        })
        .then((response) => {
          let data = response.data;
          let _account = {
            provider: {
              dataprovider_id: this.provider.dataprovider_id,
              dataprovider_name: this.provider.dataprovider_name,
            },
            username: this.j_username,
            password: this.j_password,
            name: "",
            email: "",
            phone: "",
            city: "",
            address: "",
            agent_properties: {
              dataprovider_id: this.provider.dataprovider_id,
              dataprovider_name: this.provider.dataprovider_name,
              api_auth: {
                j_username: this.j_username,
                j_password: this.j_password,
              },
            },
          };
          this.localProviderAccount = _account;
          this.localIsLoading = false;
          this.localStepValid = true;
          this.generalStore.successSnackbar(
            "Conta TerraPro verificada com sucesso!"
          );
          this.$emit("nextStep");
        })
        .catch((error) => {
          this.localIsLoading = false;
          this.generalStore.errorSnackbar(
            "Erro ao buscar informações de conta TerraPro!"
          );
        });
    },
  },
};
</script>

