<template>
  <v-row>
    <v-col cols="12" class="d-flex justify-center">
      <v-sheet rounded border class="pa-5" width="35rem">
        <v-form v-model="valid" lazy-validation>
          <div class="text-subtitle-1 text-medium-emphasis">Chave Pública</div>
          <v-text-field
            v-model="publicKey"
            class="mt-4"
            density="compact"
            placeholder="Chave Pública"
            prepend-inner-icon="mdi-key"
            variant="outlined"
            :rules="rules.publicKey"
          ></v-text-field>
          <div class="text-subtitle-1 text-medium-emphasis">Chave Privada</div>
          <v-text-field
            v-model="privateKey"
            class="mt-4"
            density="compact"
            placeholder="Chave Privada"
            prepend-inner-icon="mdi-shield-key"
            variant="outlined"
            :rules="rules.privateKey"
          ></v-text-field>
        </v-form>
        <v-btn
          block
          color="secondary"
          @click="getPESSLAccount()"
          :disabled="!valid"
          :loading="localIsLoading"
          prepend-icon="mdi-account-check"
          >Verificar conta</v-btn
        >
      </v-sheet>
    </v-col>
  </v-row>
</template>

<script>
import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import crypto from "crypto-js";
import axios from "axios";

export default {
  name: "AddPESSLAccount",
  props: {
    provider: {
      type: Object,
    },
    providerAccount: {
      type: Object,
    },
    stepValid: {
      type: Boolean,
    },
    isLoading: {
      type: Boolean,
    },
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();
    return { accountStore, generalStore };
  },
  data() {
    return {
      valid: false,
      publicKey: null,
      privateKey: null,
      rules: {
        publicKey: [(v) => !!v || "Insira a chave pública"],
        privateKey: [(v) => !!v || "Insira a chave privada"],
      },
    };
  },
  computed: {
    localProviderAccount: {
      get() {
        return this.providerAccount;
      },
      set(value) {
        this.$emit("update:providerAccount", value);
      },
    },
    localStepValid: {
      get() {
        return this.stepValid;
      },
      set(value) {
        this.$emit("update:stepValid", value);
      },
    },
    localIsLoading: {
      get() {
        return this.isLoading;
      },
      set(value) {
        this.$emit("update:isLoading", value);
      },
    },
  },
  methods: {
    getPESSLAccount() {
      this.localIsLoading = true;
      let method = "GET";
      let apiRoute = "/user";
      let dateStamp = new Date().toUTCString();
      let msg = method + apiRoute + dateStamp + this.publicKey;
      let signature = crypto.HmacSHA256(msg, this.privateKey).toString();
      axios
        .request({
          url: "https://api.fieldclimate.com/v2/user",
          method: "get",
          headers: {
            Accept: "application/json",
            "Request-Date": dateStamp,
            Authorization: "hmac " + this.publicKey + ":" + signature,
          },
        })
        .then((response) => {
          let data = response.data;
          let _account = {
            provider: this.provider,
            username: data.username,
            name: data.info.name + " " + data.info.lastname,
            email: data.info.email,
            phone: data.info.cellphone || data.info.phone,
            city: data.address.city,
            address: this.formatAddress(data.address),
            agent_properties: {
              dataprovider_id: this.provider.dataprovider_id,
              api_auth: data.api_access.hmac,
            },
          };
          this.localProviderAccount = _account;
          this.localStepValid = true;
          this.localIsLoading = false;
          this.generalStore.successSnackbar(
            "Conta PESSL verificada com sucesso!"
          );
          this.$emit("nextStep");
        })
        .catch((err) => {
          console.log(err);
          this.localIsLoading = false;
          this.generalStore.errorSnackbar(
            "Erro ao buscar informações de conta PESSL!"
          );
        });
    },
    formatAddress(address) {
      let street = address.street ? address.street + ", " : "";
      let zip = address.zip ? address.zip + " " : "";
      let city = address.city || "";
      return street + zip + city;
    },
  },
};
</script>

<style></style>
