<template>
  <v-row>
    <v-col cols="12" class="d-flex justify-center">
      <v-sheet rounded border class="pa-5" width="25rem">
        <v-form v-model="valid" lazy-validation>
          <div class="text-subtitle-1 text-medium-emphasis">Email</div>
          <v-text-field
            v-model="email"
            class="mt-4"
            density="compact"
            placeholder="Email"
            prepend-inner-icon="mdi-email"
            variant="outlined"
            :rules="rules.email"
          ></v-text-field>
          <div class="text-subtitle-1 text-medium-emphasis">Password</div>
          <v-text-field
            v-model="password"
            class="mt-4"
            density="compact"
            placeholder="Password"
            prepend-inner-icon="mdi-lock"
            variant="outlined"
            :rules="rules.password"
            :append-inner-icon="visible ? 'mdi-eye-off' : 'mdi-eye'"
            :type="visible ? 'text' : 'password'"
            @click:append-inner="visible = !visible"
          ></v-text-field>
        </v-form>
        <v-btn
          block
          color="secondary"
          @click="getWisecropAccount()"
          :disabled="!valid"
          :loading="localIsLoading"
          append-icon="mdi-account-check"
          >Verificar conta</v-btn
        >
      </v-sheet>
    </v-col>
  </v-row>
</template>

<script>
import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "AddWisecropAccount",
  props: {
    provider: {
      type: Object,
    },
    providerAccount: {
      type: Object,
    },
    stepValid: {
      type: Boolean,
    },
    isLoading: {
      type: Boolean,
    },
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return { accountStore, generalStore };
  },
  data() {
    return {
      visible: false,
      valid: false,
      email: null,
      password: null,
      rules: {
        email: [
          (v) => !!v || "Insira o email da conta Wisecrop",
          (v) => /.+@.+\..+/.test(v) || "Insira um email válido",
        ],
        password: [(v) => !!v || "Insira a senha da conta Wisecrop"],
      },
    };
  },
  computed: {
    localProviderAccount: {
      get() {
        return this.providerAccount;
      },
      set(value) {
        this.$emit("update:providerAccount", value);
      },
    },
    localStepValid: {
      get() {
        return this.stepValid;
      },
      set(value) {
        this.$emit("update:stepValid", value);
      },
    },
    localIsLoading: {
      get() {
        return this.isLoading;
      },
      set(value) {
        this.$emit("update:isLoading", value);
      },
    },
  },
  methods: {
    getWisecropAccount() {
      this.localIsLoading = true;
      axios
        .request({
          url: "api/wisecrop/auth",
          method: "POST",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: {
            email: this.email,
            password: this.password,
          },
        })
        .then((response) => {
          let data = response.data;
          let _account = {
            provider: this.provider,
            username: data.first_name,
            password: this.password,
            name: (data.first_name + " " + data.last_name).trim(),
            email: this.email,
            phone: "",
            city: "",
            address: "",
            agent_properties: {
              dataprovider_id: this.provider.dataprovider_id,
              dataprovider_name: this.provider.dataprovider_name,
              api_auth: {
                email: this.email,
                password: this.password,
              },
            },
          };
          this.localProviderAccount = _account;
          this.localStepValid = true;
          this.localIsLoading = false;
          this.generalStore.successSnackbar(
            "Conta TerraPro verificada com sucesso!"
          );
          this.$emit("nextStep");
        })
        .catch((error) => {
          this.localIsLoading = false;
          this.generalStore.errorSnackbar(
            "Erro ao buscar informações de conta WiseCrop!"
          );
        });
    },
  },
};
</script>

<style></style>
