<template>
  <v-sheet class="pa-2" elevation="8" rounded="lg" width="100%">
    <v-data-table-virtual
      fixed-headers
      no-data-text="Nenhuma conta encontrada"
      loading-text="Carregando contas..."
      :headers="headers"
      :items="tableItems"
      :loading="isLoadingAccounts"
      :height="tableHeight"
    >
      <template v-slot:item="row">
        <tr>
          <td>
            <v-btn
              color="info"
              variant="text"
              icon="mdi-information-outline"
              @click="showInfo(row.item)"
            ></v-btn>
          </td>
          <td>{{ row.item.agent_username }}</td>
          <td>{{ row.item.entity_name }}</td>
          <td>{{ row.item.entity_phone }}</td>
          <td>{{ row.item.entity_email }}</td>
          <td>{{ row.item.entity_address }}</td>
          <td>{{ row.item.entity_city }}</td>
          <td>
            <v-chip
              :color="row.item.dataprovider_name"
              variant="outlined"
              >{{ row.item.dataprovider_name }}</v-chip
            >
          </td>
          <td
            v-if="accountStore.hasRole(['Administrador'])"
            class="d-flex justify-space-evenly nowrap"
          >
            <v-btn
              color="warning"
              variant="text"
              icon
              @click="openEditDialog(row.item)"
            >
              <v-icon>mdi-pencil</v-icon>
              <v-tooltip activator="parent" location="bottom">
                Editar
              </v-tooltip>
            </v-btn>
            <v-btn
              color="error"
              variant="text"
              icon
              @click="openRemoveDialog(row.item)"
            >
              <v-icon>mdi-delete-outline</v-icon>
              <v-tooltip activator="parent" location="bottom">
                Remover
              </v-tooltip>
            </v-btn>
          </td>
        </tr>
      </template>
      <template v-if="hasMore && !isLoadingAccounts" v-slot:[`body.append`]>
        <tr
          v-intersect.quiet="{
            handler: loadMore,
            options: {
              threshold: 0.5,
            },
          }"
        >
          <td :colspan="headers.length" class="text-center">
            <v-skeleton-loader type="table-row"></v-skeleton-loader>
          </td>
        </tr>
      </template>
    </v-data-table-virtual>
    <v-dialog
      v-model="deleteDialog"
      @afterLeave="reset()"
      persistent
      width="auto"
    >
      <v-card width="40rem">
        <v-card-title class="bg-error d-flex align-center">
          <v-icon size="small" class="mr-2">mdi-delete-outline</v-icon>
          <span>Eliminar Conta</span></v-card-title
        >
        <v-card-text>
          <v-row>
            <v-col cols="6">
              <v-text-field
                v-model="account.agent_username"
                readonly
                color="primary"
                label="Nome de Utilizador"
                prepend-icon="mdi-account"
                density="compact"
                variant="outlined"
              ></v-text-field>
            </v-col>
            <v-col cols="6">
              <v-text-field
                v-model="account.entity_name"
                readonly
                color="primary"
                label="Nome"
                prepend-icon="mdi-card-account-details"
                density="compact"
                variant="outlined"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="6">
              <v-text-field
                v-model="account.entity_email"
                readonly
                color="primary"
                label="Email"
                prepend-icon="mdi-email"
                density="compact"
                variant="outlined"
              ></v-text-field>
            </v-col>
            <v-col cols="6">
              <v-text-field
                v-model="account.entity_phone"
                readonly
                color="primary"
                label="Telefone"
                prepend-icon="mdi-phone"
                counter="9"
                density="compact"
                variant="outlined"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="6">
              <v-text-field
                v-model="account.entity_address"
                readonly
                color="primary"
                label="Endereço"
                prepend-icon="mdi-map-marker"
                density="compact"
                variant="outlined"
              ></v-text-field>
            </v-col>
            <v-col cols="6">
              <v-text-field
                v-model="account.dataprovider_name"
                readonly
                color="primary"
                label="Fornecedor"
                prepend-icon="mdi-domain"
                density="compact"
                variant="outlined"
              ></v-text-field>
            </v-col>
          </v-row>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions class="d-flex justify-space-around">
          <v-btn
            color="error"
            variant="tonal"
            icon="mdi-close"
            @click="deleteDialog = false"
          ></v-btn>
          <v-btn
            color="secondary"
            variant="tonal"
            icon="mdi-check"
            :loading="isLoading"
            @click="deleteAccount()"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="editDialog"
      @afterLeave="reset()"
      persistent
      width="auto"
    >
      <v-card width="40rem">
        <v-card-title class="bg-warning d-flex align-center">
          <v-icon size="small" class="mr-2">mdi-pencil</v-icon>
          <span>Editar Conta</span>
        </v-card-title>
        <v-card-text>
          <v-form v-model="editFormValid">
            <v-row>
              <v-col cols="6">
                <v-text-field
                  v-model="account.entity_name"
                  :rules="rules.name"
                  color="primary"
                  label="Nome"
                  prepend-icon="mdi-card-account-details"
                  density="compact"
                  variant="outlined"
                ></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="6">
                <v-text-field
                  v-model="account.entity_email"
                  :rules="rules.email"
                  color="primary"
                  label="Email"
                  prepend-icon="mdi-email"
                  density="compact"
                  variant="outlined"
                ></v-text-field>
              </v-col>
              <v-col cols="6">
                <v-text-field
                  v-model="account.entity_phone"
                  :rules="rules.phone"
                  color="primary"
                  label="Telefone"
                  prepend-icon="mdi-phone"
                  counter="9"
                  density="compact"
                  variant="outlined"
                ></v-text-field>
              </v-col>
            </v-row>
            <v-row>
              <v-col>
                <v-text-field
                  v-model="account.entity_city"
                  :rules="rules.city"
                  color="primary"
                  label="Cidade"
                  prepend-icon="mdi-city"
                  density="compact"
                  variant="outlined"
                ></v-text-field>
              </v-col>
              <v-col cols="6">
                <v-text-field
                  v-model="account.entity_address"
                  :rules="rules.address"
                  color="primary"
                  label="Endereço"
                  prepend-icon="mdi-map-marker"
                  density="compact"
                  variant="outlined"
                ></v-text-field>
              </v-col>
            </v-row>
          </v-form>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions class="d-flex justify-space-around">
          <v-btn
            color="error"
            variant="tonal"
            icon="mdi-close"
            @click="editDialog = false"
          ></v-btn>
          <v-btn
            color="secondary"
            variant="tonal"
            icon="mdi-check"
            :loading="isLoading"
            :disabled="!editFormValid"
            @click="editAccount()"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-sheet>
</template>

<script>
import { useGeneralStore } from "@/stores/generalStore";
import { useAccountStore } from "@/stores/accountStore";

import axios from "axios";
export default {
  name: "ProvidersTable",
  props: {
    providers: Array,
    providersAccounts: Array,
    isLoadingAccounts: Boolean,
    hasMore: Boolean,
  },
  setup() {
    const generalStore = useGeneralStore();
    const accountStore = useAccountStore();

    return { generalStore, accountStore };
  },
  data() {
    return {
      isIntersecting: false,
      isLoading: false,
      editDialog: false,
      deleteDialog: false,
      editFormValid: false,
      account: null,
      // headers: [
      //   { title: "", align: "center" },
      //   { title: "Conta", key: "agent_username" },
      //   { title: "Nome", key: "entity_name" },
      //   { title: "Telefone", key: "entity_phone" },
      //   { title: "Email", key: "entity_email" },
      //   { title: "Endereço", key: "entity_address" },
      //   { title: "Cidade", key: "entity_city" },
      //   { title: "Fornecedor", key: "dataprovider_name" },
      //   { title: "Ações", align: "center" },
      // ],
      rules: {
        name: [(v) => !!v || "Insira o nome"],
        email: [
          (v) => !!v || "Insira o email",
          (v) => /.+@.+\..+/.test(v) || "Email inválido",
        ],
        phone: [
          (v) => !!v || "Insira o telefone",
          (v) => v.length == 9 || "Telefone inválido",
        ],
        // city: [(v) => !!v || "Insira a cidade"],
        // address: [(v) => !!v || "Insira o endereço"],
      },
    };
  },
  computed: {
    tableHeight() {
      return window.innerHeight - 260;
    },
    headers() {
      let headers = [
        { title: "", align: "center" },
        { title: "Conta", key: "agent_username" },
        { title: "Nome", key: "entity_name" },
        { title: "Telefone", key: "entity_phone" },
        { title: "Email", key: "entity_email" },
        { title: "Endereço", key: "entity_address" },
        { title: "Cidade", key: "entity_city" },
        { title: "Fornecedor", key: "dataprovider_name" },
      ];
      if (this.accountStore.hasRole(["Administrador"])) {
        headers.push({ title: "Ações", align: "center" });
      }
      return headers;
    },
    tableItems() {
      return this.providersAccounts.map((Acc) => {
        return {
          ...Acc,
          dataprovider_name: this.getDataProviderName(Acc.dataprovider_id),
        };
      });
    },
  },
  methods: {
    getDataProviderName(id) {
      return this.providers.find((provider) => provider.dataprovider_id === id)
        .dataprovider_name;
    },
    showInfo(item) {
      this.$emit("showInfo", item);
    },
    openRemoveDialog(item) {
      this.deleteDialog = true;
      this.account = Object.assign({}, item);
      console.log(this.account);
    },
    openEditDialog(item) {
      this.editDialog = true;
      this.account = Object.assign({}, item);
      console.log(this.account);
    },
    deleteAccount() {
      this.isLoading = true;
      console.log(this.account);
      axios
        .request({
          url: "api/providersaccounts/" + this.account.agent_id,
          method: "delete",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then(() => {
          this.generalStore.successSnackbar("Conta eliminada com sucesso");
          this.$emit("update:providersAccounts");
          this.isLoading = false;
          this.deleteDialog = false;
        })
        .catch((err) => {
          this.isLoading = false;
          this.generalStore.errorSnackbar("Erro ao eliminar conta");
          console.log(err);
        });
    },
    editAccount() {
      this.isLoading = true;
      let payload = {
        entity_name: this.account.entity_name,
        entity_email: this.account.entity_email,
        entity_phone: this.account.entity_phone,
        entity_address: this.account.entity_address,
        entity_city: this.account.entity_city,
      };
      console.log(payload);
      axios
        .request({
          url: "api/providersaccounts/" + this.account.agent_id,
          method: "patch",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then(() => {
          this.generalStore.successSnackbar("Conta editada com sucesso");
          this.$emit("update:providersAccounts");
          this.isLoading = false;
          this.editDialog = false;
        })
        .catch((err) => {
          this.isLoading = false;
          this.generalStore.errorSnackbar("Erro ao editar conta");
          console.log(err);
        });
    },
    loadMore(isIntersecting) {
      if (isIntersecting) {
        this.$emit("loadMore");
      }
    },
    reset() {
      this.account = null;
    },
  },
};
</script>

<style></style>
