<template>
  <!-- <h1>Contas de Fornecedores</h1> -->
  <v-container fluid class="fill-width">
    <v-row>
      <v-col cols="12">
        <v-card elevation="8" :loading="isLoadingAccounts">
          <template v-slot:loader="{ isActive }">
            <v-progress-linear
              :active="isActive"
              color="secondary"
              height="4"
              indeterminate
            ></v-progress-linear>
          </template>
          <v-card-title>Filtro</v-card-title>
          <v-card-text>
            <v-row>
              <!-- <v-btn
                  color="secondary"
                  icon
                  variant="text"
                  size="small"
                  @click="clearFilter()"
                >
                  <v-icon>mdi-broom</v-icon></v-btn
                > -->
              <v-col cols="2">
                <v-select
                  clearable
                  density="comfortable"
                  variant="underlined"
                  color="primary"
                  label="Fornecedor"
                  :disabled="isLoadingAccounts"
                  :loading="isLoadingProviders"
                  :items="filterSelectItems"
                  item-title="dataprovider_name"
                  item-value="dataprovider_id"
                  v-model="filter.provider_Id"
                ></v-select>
              </v-col>
              <v-col cols="2">
                <v-text-field
                  clearable
                  density="comfortable"
                  variant="underlined"
                  color="primary"
                  label="Conta"
                  :disabled="isLoadingAccounts"
                  v-model="filter.username"
                ></v-text-field>
              </v-col>
              <v-col cols="2">
                <v-text-field
                  clearable
                  density="comfortable"
                  variant="underlined"
                  color="primary"
                  label="Nome"
                  :disabled="isLoadingAccounts"
                  v-model="filter.name"
                ></v-text-field>
              </v-col>
              <v-col>
                <v-btn
                  color="secondary"
                  icon
                  variant="text"
                  @click="searchFitagro()"
                  ><v-icon size="x-large">mdi-magnify</v-icon></v-btn
                >
              </v-col>
              <v-col
                v-if="accountStore.hasRole(['Administrador'])"
                cols="5"
                class="d-flex justify-center"
              >
                <AddProviderAccount
                  :providers="filterSelectItems"
                  @updateAccounts="searchFitagro()"
                ></AddProviderAccount>
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <ProvidersDrawer
      :account="selectedAccount"
      :drawer="drawer"
      :accountWS="accountWS"
      :isLoading="isLoadingDrawer"
      @update="(value) => (drawer = value)"
      @reset="reset()"
    />
    <v-row align="center" class="fill-height">
      <v-col class="d-flex justify-center">
        <ProvidersTable
          :providers="providers"
          :providersAccounts="providersAccounts"
          :isLoadingAccounts="isLoadingAccounts"
          :hasMore="hasMore"
          @showInfo="showInfo($event)"
          @update:providersAccounts="searchFitagro()"
          @loadMore="getProvidersAccounts()"
        />
      </v-col>
    </v-row>
    <!-- <v-row v-if="isLoading" align="center">
      <v-col class="d-flex align-center justify-center">
        <v-progress-circular
          color="secondary"
          size="65"
          width="7"
          indeterminate
        ></v-progress-circular>
      </v-col>
    </v-row> -->
  </v-container>
</template>

<script>
import ProvidersTable from "@/components/Providers/ProvidersTable.vue";
import ProvidersDrawer from "@/components/Providers/ProvidersDrawer.vue";
import AddProviderAccount from "@/components/Providers/AddProviderAccount.vue";

import { useAccountStore } from "@/stores/accountStore";
import axios from "axios";

export default {
  name: "ProvidersView",
  components: { ProvidersTable, ProvidersDrawer, AddProviderAccount },

  setup() {
    const accountStore = useAccountStore();
    return { accountStore };
  },

  data() {
    return {
      isLoadingProviders: false,
      isLoadingAccounts: false,
      isLoadingDrawer: false,
      drawer: false,
      selectedAccount: {
        agent_id: 0,
        agent_username: "",
        dataprovider_id: 0,
        api_auth: {},
        entity_name: "",
        entity_email: "",
        entity_phone: "",
        entity_address: "",
        entity_city: "",
      },
      accountWS: {
        type: "FeatureCollection",
        features: [],
      },
      providers: [],
      providersAccounts: [],
      filter: {
        provider_Id: null,
        name: null,
        username: null,
      },
      limit: 10,
      offset: 0,
      hasMore: false,
    };
  },
  watch: {
    drawer(value) {
      if (!value) {
        this.resetDrawer();
      }
    },
  },
  computed: {
    filterSelectItems() {
      return this.providers.filter(
        (provider) =>
          provider.dataprovider_name !== "SYSTEM" &&
          provider.dataprovider_name !== "IPMA"
      );
    },
  },
  methods: {
    async getProviders() {
      this.isLoadingProviders = true;
      axios
        .request({
          url: "api/dataproviders",
          method: "get",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.providers = response.data;
          this.isLoadingProviders = false;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    async getProvidersAccounts() {
      this.isLoadingAccounts = true;
      axios
        .request({
          url: "api/providersaccounts",
          method: "get",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          params: {
            dataprovider_id: this.filter.provider_Id,
            agent_username: this.filter.username,
            entity_name: this.filter.name,
            limit: this.limit,
            offset: this.offset,
          },
        })
        .then((response) => {
          this.providersAccounts.push(...response.data.agents);
          this.offset = response.data.agents.length;
          this.hasMore = response.data.hasMore;
          this.isLoadingAccounts = false;
        });
    },
    searchFitagro() {
      this.offset = 0;
      this.providersAccounts = [];
      this.getProvidersAccounts();
    },
    clearFilter() {
      this.filter.provider_Id = null;
      this.filter.name = null;
      this.filter.username = null;
    },
    async showInfo(item) {
      this.drawer = true;
      this.selectedAccount = item;
      let dataprovider_id = item.dataprovider_id;
    },
    resetDrawer() {
      console.log("reset drawer");
      this.isLoadingDrawer = false;
      this.selectedAccount = {
        agent_id: 0,
        agent_username: "",
        dataprovider_id: 0,
        api_auth: {},
        entity_name: "",
        entity_email: "",
        entity_phone: "",
        entity_address: "",
        entity_city: "",
      };
      this.accountWS = {
        type: "FeatureCollection",
        features: [],
      };
    },
  },

  mounted() {
    this.getProviders();
    this.getProvidersAccounts();
  },
};
</script>

<style></style>
