<template>
  <v-dialog v-model="dialog" @afterleave="reset()" width="auto">
    <template v-slot:activator="{ props: activatorProps }">
      <v-btn
        fab
        v-bind="activatorProps"
        color="secondary"
        text="Nova estação"
        append-icon="mdi-plus"
      ></v-btn>
    </template>

    <template v-slot:default="{}">
      <v-card>
        <v-card-title class="bg-secondary"
          >Adicionar nova estação PESSL</v-card-title
        >
        <v-card-text>
          <v-row>
            <v-col cols="12" class="d-flex justify-center">
              <v-sheet rounded border class="pa-5" width="25rem">
                <v-form v-model="formValid" lazy-validation>
                  <div class="text-subtitle-1 text-medium-emphasis">
                    Insira o Número de Série da Estação
                  </div>
                  <v-text-field
                    v-model="serialNumber"
                    class="mt-4"
                    density="compact"
                    placeholder="Numero de Série"
                    prepend-inner-icon="mdi-numeric"
                    counter="8"
                    variant="outlined"
                    :rules="rules.serialNumber"
                  ></v-text-field>
                  <div class="text-subtitle-1 text-medium-emphasis">
                    Insira a Chave da Estação
                  </div>
                  <v-text-field
                    v-model="stationKey"
                    class="mt-4"
                    density="compact"
                    placeholder="Chave"
                    prepend-inner-icon="mdi-stationKey"
                    variant="outlined"
                    :rules="rules.stationKey"
                  ></v-text-field>
                  <div class="text-subtitle-1 text-medium-emphasis">
                    Insira o nome da estação
                  </div>
                  <v-text-field
                    v-model="customName"
                    class="mt-4"
                    density="compact"
                    placeholder="Nome da estação"
                    prepend-inner-icon="mdi-pencil"
                    variant="outlined"
                    :rules="rules.customName"
                  ></v-text-field>
                </v-form>
              </v-sheet>
            </v-col>
          </v-row>
        </v-card-text>
        <v-card-actions class="d-flex justify-space-between pa-2">
          <v-btn
            color="secondary"
            text
            @click="dialog = false"
            :disabled="isLoading"
          >
            Cancelar
          </v-btn>
          <v-btn
            color="primary"
            text
            @click="postPESSLStation()"
            :loading="isLoading"
            :disabled="!formValid"
          >
            Adicionar
          </v-btn>
        </v-card-actions>
      </v-card>
    </template>
  </v-dialog>
</template>

<script>
import { useGeneralStore } from "@/stores/generalStore";
import { useAccountStore } from "@/stores/accountStore";

import axios from "axios";
import crypto from "crypto-js";
export default {
  name: "AddWStoAccount_PESSL",
  props: {
    account: Object,
  },
  setup() {
    const generalStore = useGeneralStore();
    const accountStore = useAccountStore();
    return { generalStore, accountStore };
  },
  data() {
    return {
      isLoading: false,
      dialog: false,
      formValid: false,
      station: null,
      serialNumber: null,
      customName: null,
      stationKey: null,
      rules: {
        serialNumber: [
          (v) => !!v || "Insira o número de série",
          (v) =>
            (v && v.length <= 8) || "Número de Série deve ter 8 caracteres",
          (v) =>
            /^\d+$/.test(v) || "Número de Série deve conter apenas números",
        ],
        stationKey: [(v) => !!v || "Insira a chave"],
        customName: [
          (v) => !!v || "Insira o nome da estação",
          (v) =>
            (v && v.length <= 35) ||
            "Nome da estação deve ter no máximo 35 caracteres",
        ],
      },
    };
  },
  methods: {
    reset() {
      this.station = null;
    },
    postPESSLStation() {
      this.isLoading = true;
      axios
        .request({
          url: "api/pessl/" + this.account.agent_id + "/addstation",
          method: "post",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: {
            serialNumber: this.serialNumber,
            stationKey: this.stationKey,
            customName: this.customName,
          },
        })
        .then((response) => {
          console.log(response);
          this.dialog = false;
          this.isLoading = false;
          this.generalStore.successSnackbar(
            "Estação adicionada com sucesso à conta PESSL!"
          );
          this.$emit("updateWS", this.station);
        })
        .catch((error) => {
          console.log(error);
          this.generalStore.errorSnackbar(
            "Erro ao adicionar estação à conta PESSL!"
          );
          this.isLoading = false;
        });
    },
  },
};
</script>

<style></style>
