<template>
  <v-row>
    <v-col cols="12" class="d-flex justify-center">
      <v-sheet rounded border class="pa-5" width="40rem">
        <v-form ref="form" v-model="localStepValid">
          <v-row>
            <v-col cols="6">
              <v-text-field
                v-model.trim="providerAccount.username"
                :rules="rules.username"
                color="primary"
                label="Nome de Utilizador"
                prepend-icon="mdi-account"
                density="compact"
                variant="outlined"
              ></v-text-field>
            </v-col>
            <v-col cols="6">
              <v-text-field
                v-model.trim="providerAccount.name"
                :rules="rules.name"
                color="primary"
                label="Nome"
                prepend-icon="mdi-card-account-details"
                density="compact"
                variant="outlined"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="6">
              <v-text-field
                v-model.trim="providerAccount.email"
                :rules="rules.email"
                color="primary"
                label="Email"
                prepend-icon="mdi-email"
                density="compact"
                variant="outlined"
              ></v-text-field>
            </v-col>
            <v-col cols="6">
              <v-text-field
                v-model.trim="providerAccount.phone"
                :rules="rules.phone"
                color="primary"
                label="Telefone"
                prepend-icon="mdi-phone"
                counter="9"
                density="compact"
                variant="outlined"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="6">
              <v-text-field
                v-model.trim="providerAccount.address"
                :rules="rules.address"
                color="primary"
                label="Endereço"
                prepend-icon="mdi-map-marker"
                density="compact"
                variant="outlined"
              ></v-text-field>
            </v-col>
            <v-col cols="6">
              <v-text-field
                v-model="providerAccount.provider.dataprovider_name"
                :rules="rules.provider"
                color="primary"
                label="Fornecedor"
                prepend-icon="mdi-domain"
                readonly
                density="compact"
                variant="outlined"
              ></v-text-field>
            </v-col>
          </v-row>
        </v-form>
      </v-sheet>
    </v-col>
  </v-row>
</template>

<script>
export default {
  name: "AccountForm",
  props: {
    providerAccount: {
      type: Object,
    },
    stepValid: {
      type: Boolean,
    },
  },
  data() {
    return {
      rules: {
        username: [
          (v) => !!v || "Insira o nome de utilizador",
          (v) => v.length <= 40 || "Nome de utilizador muito longo",
        ],
        name: [
          (v) => !!v || "Insira o nome",
          (v) => v.length <= 20 || "Nome muito longo",
        ],
        email: [
          (v) => !!v || "Insira o email",
          (v) => /.+@.+\..+/.test(v) || "Insira um gmail válido",
        ],
        phone: [
          (v) => !!v || "Insira o telefone",
          (v) => v.length == 9 || "Telefone inválido",
        ],
        // address: [(v) => !!v || "Insira o endereço"],
        provider: [(v) => !!v || "Insira o fornecedor"],
      },
    };
  },
  computed: {
    localProviderAccount: {
      get() {
        return this.providerAccount;
      },
      set(value) {
        this.$emit("update:providerAccount", value);
      },
    },
    localStepValid: {
      get() {
        return this.stepValid;
      },
      set(value) {
        this.$emit("update:stepValid", value);
      },
    },
  },
  mounted() {
    this.$refs.form.validate();
  },
};
</script>

<style></style>
