<template>
  <v-dialog v-model="mainDialog" @afterLeave="reset()" width="auto">
    <template v-slot:activator="{ props: activatorProps }">
      <v-btn
        v-bind="activatorProps"
        color="success"
        text="Nova Conta"
        append-icon="mdi-plus"
      ></v-btn>
    </template>
    <template v-slot:default="{}">
      <v-stepper v-model="currStep" :items="steps" width="50rem" ref="stepper">
        <template v-slot:item.1>
          <v-row>
            <v-col cols="12" class="d-flex justify-center">
              <v-sheet rounded border class="pa-5" width="25rem">
                <v-form v-model="steps[0].stepValid" lazy-validation>
                  <div>
                    <span class="text-subtitle-1 text-medium-emphasis">
                      Selecione um Fornecedor
                    </span>
                    <v-select
                      class="mt-4"
                      density="compact"
                      variant="outlined"
                      prepend-inner-icon="mdi-domain"
                      label="Fornecedor"
                      :items="providerSelectItems"
                      :rules="steps[0].rules.provider"
                      item-title="dataprovider_name"
                      v-model="providerSelected"
                      return-object
                    ></v-select>
                  </div>
                </v-form>
              </v-sheet>
            </v-col>
          </v-row>
        </template>
        <template v-slot:item.2>
          <div v-if="providerNameSelected == 'PESSL'">
            <v-row>
              <v-col cols="12"
                ><AddPESSLAccount
                  :provider="providerSelected"
                  :providerAccount="providerAccount"
                  :stepValid="steps[1].stepValid"
                  :isLoading="steps[1].isLoading"
                  @nextStep="stepper.next()"
                  @update:providerAccount="(value) => (providerAccount = value)"
                  @update:stepValid="(value) => updateStepValid(1, value)"
                  @update:isLoading="(value) => updateIsLoading(1, value)"
                ></AddPESSLAccount
              ></v-col>
              <v-col v-if="providerAccount != null" cols="6"></v-col>
            </v-row>
          </div>
          <div v-else-if="providerNameSelected == 'APAS'">
            <v-row>
              <v-col cols="12"
                ><AddAPASAccount
                  :provider="providerSelected"
                  :providerAccount="providerAccount"
                  :stepValid="steps[1].stepValid"
                  :isLoading="steps[1].isLoading"
                  @nextStep="stepper.next()"
                  @update:providerAccount="(value) => (providerAccount = value)"
                  @update:stepValid="(value) => updateStepValid(1, value)"
                  @update:isLoading="(value) => updateIsLoading(1, value)"
                ></AddAPASAccount
              ></v-col>
              <v-col v-if="providerAccount != null" cols="6"></v-col>
            </v-row>
          </div>
          <div v-else-if="providerNameSelected == 'WiseCrop'">
            <AddWisecropAccount
              :provider="providerSelected"
              :providerAccount="providerAccount"
              :stepValid="steps[1].stepValid"
              :isLoading="steps[1].isLoading"
              @nextStep="stepper.next()"
              @update:providerAccount="(value) => (providerAccount = value)"
              @update:stepValid="(value) => updateStepValid(1, value)"
              @update:isLoading="(value) => updateIsLoading(1, value)"
            ></AddWisecropAccount>
          </div>
          <div v-else-if="providerNameSelected == 'TerraPro'">
            <AddTerraproAccount
              :provider="providerSelected"
              :providerAccount="providerAccount"
              :stepValid="steps[1].stepValid"
              :isLoading="steps[1].isLoading"
              @nextStep="stepper.next()"
              @update:providerAccount="(value) => (providerAccount = value)"
              @update:stepValid="(value) => updateStepValid(1, value)"
              @update:isLoading="(value) => updateIsLoading(1, value)"
            ></AddTerraproAccount>
          </div>
        </template>
        <template v-slot:item.3>
          <AccountForm
            :providerAccount="providerAccount"
            :stepValid="steps[2].stepValid"
            @update:providerAccount="(value) => (providerAccount = value)"
            @update:stepValid="(value) => updateStepValid(2, value)"
          ></AccountForm>
        </template>
        <template v-slot:actions>
          <div class="d-flex justify-space-between pa-2">
            <v-btn
              color="info"
              variant="tonal"
              prepend-icon="mdi-arrow-left"
              @click="stepper.prev()"
              :disabled="currStep == 1"
              >Anterior</v-btn
            >
            <v-btn
              v-if="currStep < lastStep"
              color="info"
              variant="tonal"
              append-icon="mdi-arrow-right"
              :disabled="!steps[currStep - 1].stepValid"
              :loading="steps[currStep - 1].isLoading"
              @click="stepper.next()"
              >Próximo</v-btn
            >
            <v-btn
              v-else
              color="success"
              variant="flat"
              append-icon="mdi-check"
              :disabled="!steps[currStep - 1].stepValid"
              :loading="steps[currStep - 1].isLoading"
              @click="submitAcc()"
              >Adicionar</v-btn
            >
          </div>
        </template>
      </v-stepper>
    </template>
  </v-dialog>
</template>

<script>
import AddPESSLAccount from "./AddProviderAccount/AddPESSLAccount.vue";
import AddAPASAccount from "./AddProviderAccount/AddAPASAccount.vue";
import AddWisecropAccount from "./AddProviderAccount/AddWisecropAccount.vue";
import AddTerraproAccount from "./AddProviderAccount/AddTerraproAccount.vue";
import AccountForm from "./AddProviderAccount/AccountForm.vue";

import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

import { ref } from "vue";

export default {
  name: "AddProviderAccount",

  props: {
    providers: Array,
  },
  components: {
    AddPESSLAccount,
    AddAPASAccount,
    AddWisecropAccount,
    AddTerraproAccount,
    AccountForm,
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();
    const stepper = ref(null);
    return { accountStore, generalStore, stepper };
  },
  data() {
    return {
      mainDialog: false,
      isLoading: false,
      currStep: 1,
      steps: [
        {
          title: "Fornecedor",
          rules: {
            provider: [(v) => !!v || "Selecione um fornecedor"],
            proAcc: [(v) => !!v || "Selecione uma conta"],
          },
          stepValid: false,
          isLoading: false,
        },
        {
          title: "Conta",
          stepValid: false,
          isLoading: false,
        },
      ],
      providerSelected: null,
      providerAccount: null,
    };
  },
  watch: {
    providerSelected() {
      this.providerAccount = null;
      switch (this.providerNameSelected) {
        case "PESSL":
          this.steps = [
            {
              title: "Fornecedor",
              rules: {
                provider: [(v) => !!v || "Selecione um fornecedor"],
                proAcc: [(v) => !!v || "Selecione uma conta"],
              },
              stepValid: false,
              isLoading: false,
            },
            {
              title: "Chaves de Acesso",
              stepValid: false,
              isLoading: false,
            },
            {
              title: "Conta PESSL",
              stepValid: false,
              isLoading: false,
            },
          ];
          break;
        case "APAS":
          this.steps = [
            {
              title: "Fornecedor",
              rules: {
                provider: [(v) => !!v || "Selecione um fornecedor"],
                proAcc: [(v) => !!v || "Selecione uma conta"],
              },
              stepValid: false,
              isLoading: false,
            },
            {
              title: "Código Dropbox",
              stepValid: false,
              isLoading: false,
            },
            {
              title: "Conta APAS",
              stepValid: false,
              isLoading: false,
            },
          ];
          break;
        case "WiseCrop":
          this.steps = [
            {
              title: "Fornecedor",
              rules: {
                provider: [(v) => !!v || "Selecione um fornecedor"],
                proAcc: [(v) => !!v || "Selecione uma conta"],
              },
              stepValid: false,
              isLoading: false,
            },
            {
              title: "Credenciais WiseCrop",
              stepValid: false,
              isLoading: false,
            },
            {
              title: "Conta WiseCrop",
              stepValid: false,
              isLoading: false,
            },
          ];
          break;
        case "TerraPro":
          this.steps = [
            {
              title: "Fornecedor",
              rules: {
                provider: [(v) => !!v || "Selecione um fornecedor"],
                proAcc: [(v) => !!v || "Selecione uma conta"],
              },
              stepValid: false,
              isLoading: false,
            },
            {
              title: "Credenciais TerraPro",
              stepValid: false,
              isLoading: false,
            },
            {
              title: "Conta TerraPro",
              stepValid: false,
              isLoading: false,
            },
          ];
          break;
        default:
          this.steps = [
            {
              title: "Fornecedor",
              rules: {
                provider: [(v) => !!v || "Selecione um fornecedor"],
                proAcc: [(v) => !!v || "Selecione uma conta"],
              },
              stepValid: false,
              isLoading: false,
            },
            {
              title: "Conta",
              stepValid: false,
              isLoading: false,
            },
          ];
      }
    },
  },
  computed: {
    providerSelectItems() {
      return this.providers.filter(
        (provider) => provider.dataprovider_name !== "APAS"
      );
    },
    providerIdSelected() {
      return this.providerSelected
        ? this.providerSelected.dataprovider_id
        : null;
    },
    providerNameSelected() {
      return this.providerSelected
        ? this.providerSelected.dataprovider_name
        : null;
    },
    lastStep() {
      return this.steps.length;
    },
  },
  methods: {
    submitAcc() {
      switch (this.providerIdSelected) {
        case 1:
          this.postPESSLAccountFitoagro();
          break;
        default:
          this.postAccountToFitoagro();
          break;
      }
    },
    postAccountToFitoagro() {
      this.steps[2].isLoading = true;
      let payload = {
        agenttype_id: 2,
        entity_name: this.providerAccount.name,
        entity_phone: this.providerAccount.phone,
        entity_email: this.providerAccount.email,
        entity_address: this.providerAccount.address,
        entity_city: this.providerAccount.city,
        agent_username: this.providerAccount.username,
        agent_accepted: true,
        agent_blocked: false,
        agent_active: true,
        agent_password: this.providerAccount.name + this.providerAccount.phone,
        agent_properties: this.providerAccount.agent_properties,
      };
      console.log(payload);
      axios
        .request({
          url: "api/providersaccounts",
          method: "post",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then((response) => {
          this.steps[2].isLoading = false;
          this.mainDialog = false;
          this.generalStore.successSnackbar("Conta adicionada com sucesso!");
          this.$emit("updateAccounts");
        })
        .catch((error) => {
          console.log(error);
          this.steps[2].isLoading = false;
          this.generalStore.errorSnackbar("Erro ao adicionar conta!");
        });
    },
    postPESSLAccountFitoagro() {
      this.steps[2].isLoading = true;
      let payload = {
        // agenttype_id: 2,
        entity_name: this.providerAccount.name,
        entity_phone: this.providerAccount.phone,
        entity_email: this.providerAccount.email,
        entity_address: this.providerAccount.address,
        entity_city: this.providerAccount.city,
        agent_username: this.providerAccount.username,
        agent_accepted: true,
        agent_blocked: false,
        agent_active: true,
        agent_password:
          this.providerAccount.agent_properties.api_auth.private_key,
        agent_properties: this.providerAccount.agent_properties,
      };
      console.log(payload);
      axios
        .request({
          url: "api/providersaccounts",
          method: "post",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then((response) => {
          console.log(response);
          this.steps[2].isLoading = false;
          this.mainDialog = false;
          this.generalStore.successSnackbar("Conta adicionada com sucesso!");
          this.$emit("updateAccounts");
        })
        .catch((error) => {
          console.log(error);
          this.steps[2].isLoading = false;
          this.generalStore.errorSnackbar("Erro ao adicionar conta!");
        });
    },
    updateStepValid(step, value) {
      this.steps[step].stepValid = value;
    },
    updateIsLoading(step, value) {
      this.steps[step].isLoading = value;
    },
    reset() {
      console.log("reset");
      this.currStep = 1;
      this.providerSelected = null;
      this.providerAccount = null;
    },
  },
};
</script>

<style></style>
