<template>
  <v-container class="fill-height">
    <v-row v-if="isLoading" align="center">
      <v-col class="d-flex align-center justify-center">
        <v-progress-circular
          color="secondary"
          size="65"
          width="7"
          indeterminate
        ></v-progress-circular>
      </v-col>
    </v-row>
    <v-row v-show="!isLoading" class="fill-height">
      <v-col cols="6">
        <v-sheet class="pa-2" elevation="8" rounded="lg">
          <v-data-table-virtual
            class="fill-height"
            :headers="headers"
            :items="displayItems"
            fixed-header
            :height="sheetHeight"
          >
            <template v-slot:item="row">
              <tr>
                <td>{{ row.item.datasource_name }}</td>
                <td>{{ row.item.datasource_externalId }}</td>
                <td class="text-center">
                  <v-icon v-if="row.item.isFitoagro" color="green"
                    >mdi-check</v-icon
                  >
                  <v-icon v-else color="red">mdi-close</v-icon>
                </td>
                <td class="text-center">
                  <v-btn
                    color="blue"
                    variant="text"
                    icon
                    @click="showOnMap(row.item)"
                  >
                    <v-icon>mdi-crosshairs-gps</v-icon>
                    <v-tooltip activator="parent" location="bottom"
                      >{{ row.item.location }}
                    </v-tooltip>
                  </v-btn>
                </td>
              </tr>
            </template>
          </v-data-table-virtual>
        </v-sheet>
      </v-col>
      <v-col cols="6">
        <v-sheet class="pa-2" elevation="8" rounded="lg">
          <MapboxMap
            ref="mapboxMap"
            :accessToken="accessToken"
            mapStyle="mapbox://styles/mapbox/satellite-streets-v12"
            @mb-created="
              (mapInstance) => {
                map = mapInstance;
              }
            "
            :center="mapCenter"
            :zoom="8"
            :style="{ height: sheetHeight + 'px' }"
          >
          </MapboxMap>
        </v-sheet>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import { MapboxMap } from "@studiometa/vue-mapbox-gl";
import mapboxgl from "mapbox-gl";

import axios from "axios";
export default {
  name: "TerraproDrawer",
  components: { MapboxMap },
  props: {
    account: {
      type: Object,
    },
    drawer: {
      type: Boolean,
    },
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    const popup = new mapboxgl.Popup({
      closeButton: false,
      closeOnClick: false,
    });
    const accessToken = process.env.VUE_APP_MAPBOX_TOKEN;
    return { accountStore, generalStore, popup, accessToken };
  },
  data() {
    return {
      isLoading: true,
      accountWS: {
        type: "FeatureCollection",
        features: [],
      },
      fitoagroWS: {
        type: "FeatureCollection",
        features: [],
      },
      headers: [
        { title: "Nome", key: "datasource_name" },
        { title: "ID", key: "datasource_externalId" },
        { title: "Fitoagro", value: "fitoagro", align: "center" },
        { title: "Localização", value: "location", align: "center" },
      ],
      map: null,
      mapCenter: [-9, 39],
    };
  },
  watch: {
    mapItems: {
      handler: function (newVal, oldVal) {
        if (newVal.features.length > 0 && this.map.getSource("ws"))
          this.map.getSource("ws").setData(newVal);
      },
      deep: true,
    },
    drawer: {
      handler: function (newVal, oldVal) {
        if (!newVal) {
          this.reset();
        }
      },
    },
  },
  computed: {
    sheetHeight() {
      return window.innerHeight - 200;
    },
    displayItems() {
      return this.accountWS.features.map((ws) => {
        return {
          datasource_name: ws.properties.datasource_name,
          datasource_externalId: ws.properties.datasource_externalId,
          isFitoagro: this.belongsToFitoagro(
            ws.properties.datasource_externalId
          ),
          location: ws.geometry.coordinates,
        };
      });
    },
    mapItems() {
      return {
        type: "FeatureCollection",
        features: this.displayItems.map((ws) => {
          return {
            type: "Feature",
            geometry: {
              type: "Point",
              coordinates: ws.location,
            },
            properties: {
              datasource_name: ws.datasource_name,
              datasource_externalId: ws.datasource_externalId,
              isFitoagro: ws.isFitoagro.toString(),
            },
          };
        }),
      };
    },
  },
  methods: {
    async getTerraproWeatherStations() {
      this.isLoading = true;
      axios
        .request({
          url: "api/terrapro/" + this.account.agent_id + "/stations",
          method: "get",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.accountWS = {
            type: "FeatureCollection",
            features: response.data.map((station) => {
              return {
                type: "Feature",
                geometry: {
                  type: "Point",
                  coordinates: station.location,
                },
                properties: {
                  datasource_name: station.stationName,
                  datasource_externalId: station.stationID,
                },
              };
            }),
          };
          this.isLoading = false;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    async getStationsFitoagro() {
      let _provider_Id = this.account.dataprovider_id;
      await axios
        .request({
          url: "api/dataproviders/" + _provider_Id + "/datasources",
          method: "get",
          params: {
            datasourcetype_id: this.WSfromFitoagro_id,
          },
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.fitoagroWS = {
            type: "FeatureCollection",
            features: response.data.datasources.map((item) => ({
              type: "Feature",
              properties: {
                datasource_id: item.datasource_id,
                datasource_name: item.datasource_name,
                dataprovider_id: item.dataprovider_id,
                datasource_externalId: item.datasource_externalid,
              },
              geometry: item.datasource_location,
            })),
          };
        })
        .catch((error) => {
          console.log(error);
          this.generalStore.errorSnackbar(
            "Erro ao carregar estações meteorológicas"
          );
        });
    },
    belongsToFitoagro(externalId) {
      return this.fitoagroWS.features.some(
        (ws) => ws.properties.datasource_externalId === externalId
      );
    },
    showOnMap(item) {
      this.map.flyTo({
        center: item.location,
        zoom: 12,
      });
    },
    openDrawer() {
      this.isLoading = true;
      this.getStationsFitoagro().then(() => {
        this.getTerraproWeatherStations().then(() => {});
      });
    },
    reset() {
      this.accountWS = {
        type: "FeatureCollection",
        features: [],
      };
      this.fitoagroWS = {
        type: "FeatureCollection",
        features: [],
      };
      this.map.setCenter(this.mapCenter);
      this.map.setZoom(8);
    },
  },
  created() {
    this.openDrawer();
  },
  mounted() {
    this.map.on("load", () => {
      this.map.addSource("ws", {
        type: "geojson",
        data: this.mapItems,
      });
      this.map.addLayer({
        id: "ws",
        type: "circle",
        source: "ws",
        paint: {
          "circle-radius": 6,
          "circle-color": [
            "match",
            ["get", "isFitoagro"],
            "true",
            "darkgreen",
            "false",
            "red",
            "blue",
          ],
          "circle-stroke-width": 1,
          "circle-stroke-color": "#fff",
        },
      });
      this.map.on("mouseenter", "ws", (e) => {
        this.map.getCanvas().style.cursor = "pointer";
        let coordinates = e.features[0].geometry.coordinates.slice();
        let description =
          "<b>Nome:</b> " +
          e.features[0].properties.datasource_name +
          "<br> <b>ID:</b> " +
          e.features[0].properties.datasource_externalId;
        this.popup.setLngLat(coordinates).setHTML(description).addTo(this.map);
      });
      this.map.on("mouseleave", "ws", () => {
        this.map.getCanvas().style.cursor = "";
        this.popup.remove();
      });
    });
  },
};
</script>

<style></style>
